@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.pointer, .pointer * {
  cursor: pointer;
}

.fp-btn, .fp-btn:active, .fp-btn:focus {
  padding: 10px 20px;
  border-radius: 10px;
  text-align: center;
  transition: all .3s ease-in-out;
  background-color: transparent;
  border: solid 2px;
  outline: none;
}

.fp-btn.btn-more {
  font-family: "open_sansbold";
  color: #fff;
  border-color: #e3000f;
  background-color: #e3000f;
}

.fp-btn.btn-more:hover {
  color: #e3000f;
  background-color: #fff;
}

.hot {
  position: absolute;
  right: 0;
  bottom: 0;
  margin: 10px;
  padding: 5px;
  width: 40px;
  height: 40px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../images/hot.svg");
  background-origin: content-box;
  background-color: white;
  border-radius: 50%;
}

a {
  text-decoration: solid underline #e3000f;
}

a:hover, a:focus {
  text-decoration: solid underline #e3000f;
}

#btn_more .spinner {
  display: none;
  top: 50%;
  left: 20px;
  width: 20px;
  height: 20px;
  transform: translate(-50%, -50%);
}

#btn_more .spinner .box {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-style: solid;
  border-color: #fff;
  border-width: 3px 0px 3px 0px;
  border-radius: 50%;
  background-color: #e3000f;
}

#btn_more.loading .spinner {
  display: block;
}

#btn_more.loading .spinner .box {
  animation-name: spin;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-fill-mode: both;
  animation-direction: normal;
}

#search .tile {
  padding: 1.2rem 1rem;
}

#search .tile .img {
  width: 160px;
  height: 90px;
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../images/no-photo.png);
}

#search .tile .title {
  padding-left: 20px;
}

#search .tile, #search .tile:hover, #search .tile:focus {
  text-decoration: none;
}
